
import XNumber from 'vux/src/components/x-number/index.vue'

export default {
name: "goodsDetail",
components: {
},
data() {
return {
selected: true,
roundValue: 0,
proNum: 0,
}
},
computed:{
productNum(){
return this.$store.state.goodsNum;
}
},
methods: {
goToCar () {
this.$router.push({path: '/mainpage/car'})
},
collect () {
this.selected = !this.selected;
},
addReduce (isAdd) {
if (isAdd) {
this.proNum += 1;
this.$store.commit('add');
} else {
if(this.proNum > 0) {
this.proNum -= 1;
this.$store.commit('reduce');
}
}
},

resetNum (sid,id) {
let self = this, Num = 0;
let car = this.$store.state.car;
if (car.length === 0)
return
car.forEach(element => {
if(element.id === id && element.specificsChild === sid) {
Num = element.proNum;
}
});
self.proNum = Num;
}
},
created() {
console.log('goodsDetail===================created')
},
mounted () {
console.log('goodsDetail===================mounted')
},
activated () {

console.log('goodsDetail===================activated')
},
deactivated () {
console.log('goodsDetail===================deactivated')
},
destroyed () {
console.log('goodsDetail===================destroyed')
},
};



